@font-face {
    font-family: 'Cursed Timer';
    src: url(../fonts/cursed-timer.ttf);
}

#timer-display {
    font-size: 150px;
    min-height: 228px;
    border: 2px solid gray;
    border-radius: 5px;
}

#timer-display-min::after {
    content: ':';
}

#timer-display-min, #timer-display-sec, #timer-display-ms {
    font-family: 'Cursed Timer', 'Titillium Web', Arial, sans-serif;
}

#timer-display-signal {
    font-family: 'Titillium Web', Arial, sans-serif;
}

#timer-display-ms {
    font-size: 50px;
}

#timer-display-ms::before {
    content: ':';
}

#timer-control-panel {
    text-align: left;
}

#timer-status-container {
    font-size: 30px;
}

p {
    margin: 0;
}

.timer-button {
    margin: 5px;
    padding: 15px;
    border: 2px solid black;
    border-radius: 10%;
    /* transition: background 1s; */
}

#play-button, #pause-button {
    background: #BFB;
}

#stop-button {
    background: #FBB;
}

#play-button:hover, #pause-button:hover {
    background: #6D6;
}

#stop-button:hover {
    background: #D66;
}
